import { request, requestDownload } from 'utils';

const prefix = '/api/organization';

export const getDocOut = paramsSearch =>
  request({
    prefix,
    url: `/DocOut`,
    params: { ...paramsSearch },
    method: 'GET',
  });

export const createDocOut = formData =>
  request({
    prefix,
    url: `/DocOut`,
    data: formData,
    method: 'POST',
  });

export const deleteDocOut = id =>
  request({
    prefix,
    url: `/DocOut`,
    params: { id },
    method: 'DELETE',
  });

export const getDocOutId = id =>
  request({
    prefix,
    url: `/DocOut/GetById`,
    params: { id },
    method: 'GET',
  });

export const updateDocOut = editDoc =>
  request({
    prefix,
    url: `/DocOut`,
    data: editDoc,
    method: 'PUT',
  });

export const downloadFile = paramsSearch =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportDocOuts.xlsx',
    url: `/DocOut/ExportDocOuts`,
    data: { ...paramsSearch },
    method: 'POST',
  });



