import React, { Component } from 'react';
import routeConstant from 'routes/route/routeConstant';

import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Upload, Input, message, Checkbox, Button, Icon, Select } from 'antd';
import moment from 'moment';
import { DatePickerString } from 'components/common';
import { createDocOut, getDocOutId, updateDocOut } from 'services/docOut';
import { getDocIns } from 'services/docIn';
import { getQueryParams } from 'utils/urlUtil';

const formatDate = 'YYYY-MM-DD';
const { Option } = Select;
const TIME_RETURN = 1500;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    className: '',
    regNum: '',
    decDay: '',
    rewardLevel: '',
    form: '',
    typeOfReward: '',
    file: {},
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddReward extends Component {
  state = {
    teacherId: false,
    queryParamId: false,
    initialValue: {},
    listOption: [],
    loading: false,
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();

    if (queryParamId) {
      this.setState({ teacherId: queryParamId, queryParamId });
      this.getDataById(queryParamId);
    }
    this.loadDoc('');
    this.props.form.setFieldsValue({
      docOutDate: moment(new Date()),
    });
    this.props.form.setFieldsValue({
      noteDate: moment(new Date()),
    });
  };

  getDataById = async id => {
    const res = await getDocOutId(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        this.setState({ loading: true });
        const formData = new FormData();
        formData.append('id', values.id || null);
        formData.append('docOutDate', moment(values.docOutDate).format(formatDate));
        formData.append('docInId', values.docInId || '');
        formData.append('sentUnit', values.sentUnit || '');
        formData.append('noteDate', moment(values.noteDate).format(formatDate));
        formData.append('docContent', values.docContent || '');
        formData.append('isSentEmail', values.isSentEmail || false);
        formData.append('recipientPlace', values.recipientPlace || '');
        formData.append('savedPlace', values.savedPlace || '');
        formData.append('formFile', (values.formFile && values.formFile[0].originFileObj) || '');
        if (this.state.queryParamId) {
          await updateDocOut(formData)
            .then(() => {
              message.success('Chỉnh sửa hồ sơ đi thành công');
              setTimeout(
                () => this.props.history.push(routeConstant.TO_CHUC__CONG_VAN__CONG_VAN_DI),
                TIME_RETURN
              );
            })
            .catch(error => {
              console.log('error', error);
            });
        } else {
          await createDocOut(formData)
            .then(() => {
              message.success('Thêm hồ sơ đi thành công');
              setTimeout(
                () => this.props.history.push(routeConstant.TO_CHUC__CONG_VAN__CONG_VAN_DI),
                TIME_RETURN
              );
            })
            .catch(error => {
              console.log('res', error.response);
            });
        }
        this.setState({ loading: false });
      }
    });
  };

  loadDoc = docId => {
    this.props.form.resetFields(['docInId']);
    getDocIns({ docInNo: docId, isCompleted: false }).then(res => {
      this.setState({
        listOption: res.data.items,
      });
    });
  };

  normFile = e => {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  render() {
    const { loading } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { initialValue, teacherId, listOption } = this.state;
    return (
      <Card title={`${teacherId ? 'Cập nhật' : 'Thêm'} khu vực`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Số công văn">
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={this.state.queryParamId} />)}
            </Form.Item>
            <Form.Item label="Ngày đi ">
              {getFieldDecorator('docOutDate', {
                initialValue: initialValue.docOutDate,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<DatePickerString placeholder="Chọn ngày" />)}
            </Form.Item>
            <Form.Item label="Chọn CV">
              {getFieldDecorator('docInId', {
                initialValue: initialValue.docInId,
              })(
                <Select placeholder="Chọn công văn">
                  {listOption.map(item => (
                    <Option key={item.id}>{item.docInNo}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item label="Đơn vị gởi">
              {getFieldDecorator('sentUnit', {
                initialValue: initialValue.sentUnit,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Ngày vào sổ">
              {getFieldDecorator('noteDate', {
                initialValue: initialValue.noteDate,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<DatePickerString placeholder="Chọn ngày" />)}
            </Form.Item>
            <Form.Item label="Nội dung">
              {getFieldDecorator('docContent', {
                initialValue: initialValue.docContent,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Đã gởi email">
              {getFieldDecorator('isSentEmail', {
                initialValue: initialValue.isSentEmail,
                valuePropName: 'checked',
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item label="Nơi nhận">
              {getFieldDecorator('recipientPlace', {
                initialValue: initialValue.recipientPlace,
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Nơi lưu">
              {getFieldDecorator('savedPlace', {
                initialValue: initialValue.savedPlace,
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Upload File">
              {getFieldDecorator('formFile', {
                valuePropName: 'fileList',
                getValueFromEvent: this.normFile,
              })(
                <Upload
                  action="/upload.do"
                  name="file"
                  listType="text"
                  onRemove={this.handleRemoveFile}
                  beforeUpload={() => false}
                >
                  <Button>
                    <Icon type="upload" /> Upload
                  </Button>
                </Upload>
              )}
            </Form.Item>

            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" loading={loading} />
              <BtnCancel loading={loading} onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddReward {...props} />
  </Page>
));



